<template>
  <div class="scanresult">
        <vue-headful
            title="0xsp Mongoose | Results"
            description=""
        />             
    <div v-if="rows">
      <b-card no-body class="mb-1" v-for="row in rows" :key="row.id">
        <b-card-header header-tag="header" class="p-1" role="tab">
          <b-button block v-b-toggle="'collapse-' + row.id" variant="info"
            >Report At: {{ row.created_at }}</b-button
          >
        </b-card-header>
        <b-collapse
          :id="'collapse-' + row.id" 
          accordion="accordion"
          role="tabpanel"
        >
          <b-card-body  class="p-0 m-0 result" v-html="row.body">
          </b-card-body>
        </b-collapse>
      </b-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rows: [],
      hostKey: this.$route.params.id,
      scanType: this.$route.params.cat,
      scanName: null,
    };
  },
  methods: {
    async getScanData() {
      this.axios
        .get("/mydashboard/scanresult/" + this.scanType + "/" + this.hostKey)
        .then(
          (response) => (
            (this.rows = response.data.data),
            (this.scanName = response.data.catName)
          )
        );
    },
  },
  created() {
    this.getScanData();
  },
};
</script>

<style >
.result{
    font-family: 'Ubuntu', sans-serif !important;
    display: block;
    font-family: Consolas, Lucida Console, monospace;
    font-size: 16px;
    width: 100%;
    line-height:2;
    background: rgb(49, 47, 47);
}
pre{
    color: #ffffff;
    padding: 15px;
}
dir{
    all: unset;
  }
</style>
